<template>
  <div class="container">
    <h1>Latest Posts</h1>

    <label for="post-field">Create Post: </label>
    <input id="post-field" v-model="text" type="text">
    <button @click="createPost">Post</button>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>

    <div class="posts-container">
      <div class="post" v-for="(post, index) in posts" :key="post._id" :index="index" :post="post">
        {{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}` }}
        <p class="text">{{ post.text}}</p>
        <button @click="deletePost(post._id)">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService";
export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  },

  methods: {
    async createPost() {
      if (this.text != "") {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts(); 
      this.text = "";
      }
      
    },

    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}
</script>

